import React, { PropTypes } from 'react';
import FormView from './formView';
import InputField from './inputField';


export default (props) => {
    const { onChange, stepForward, stepBackward, maxValue, tickInterval } = props;
    return (
        <FormView title="Параметры эксперимента">
            <InputField label="Время тика (сек)" value={ tickInterval }
                        onChange={ onChange } id="tickInterval"
            />
            <InputField label="Максимальный размер увеличения показателя за 1 тик"
                        value={ stepForward }
                        onChange={ onChange } id="stepForward"
            />
            <InputField label="Максимальный размер уменьшения показателя за 1 тик"
                        value={ stepBackward }
                        onChange={ onChange } id="stepBackward"
            />
            <InputField label="Отсчет времени реагирования начинается после появления этого числа в одном из датчиков"
                        value={ maxValue }
                        onChange={ onChange } id="maxValue"
            />
        </FormView>
    );
}


