require('../../node_modules/bootstrap/dist/css/bootstrap.min.css');
require('../css/main.scss');


import React from 'react';
import Cell from './cell';
import InputField from './inputField';
import ExperimentForm from './experimentParamsForm';
import ModelForm from './modelParamsForm';


class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            cell: {
                width: 50,
                height: 50,
                background: 'white',
                color: 'black',
                fontSize: 25,
                borderColor: 'black'
            },

            result: false,
            errorStartTime: 0,

            maxValue: 20,
            cellsPerLine: 3,
            linesCount: 3,
            tick: false,
            tickInterval: 1,
            stepBackward: 3,
            stepForward: 5
        };
        this.cells = [];
        this.onChange = this.onChange.bind(this);
        this.start = this.start.bind(this);
    }

    start() {
        if (this.state.started) {
            const errorStartTime = this.state.errorStartTime;
            clearInterval(this.tickInterval);
            this.setState({
                started: false,
                errorStartTime: 0,
                result: errorStartTime > 0 ?
                    `${(new Date() * 1000 - errorStartTime) / (1000 * 1000)} сек` :
                    'Ошибки еще не было'
            });
            this.cells = this.cells.map(x => 0);
            return;
        }
        this.tickInterval = setInterval(
            () => {
                const maxValue = parseInt(this.state.maxValue);
                let errorStartTime = this.state.errorStartTime;
                // Пересчитываем значения
                let hasMaximum = errorStartTime !== 0;
                this.cells = this.cells.map(x => {
                    let newValue = Math.random() > 0.5 ?
                        x + Math.round(Math.random() * this.state.stepForward) :
                        x - Math.round(Math.random() * this.state.stepBackward);

                    if (x >= maxValue) {
                       debugger; 
                    }
                    if (newValue >= maxValue){
                        debugger;
                    }
                    newValue = x >= maxValue && newValue < maxValue ? x : newValue;
                    if (newValue >= maxValue) {
                        if (hasMaximum && x < maxValue) {
                            newValue = maxValue - Math.round(Math.random() * this.state.stepForward) - 1;
                        } else {
                            hasMaximum = true;
                        }
                    }
                    return newValue;
                });
                // Засекаем начальное появление ошибки
                if (this.cells.some(x => x >= parseInt(this.state.maxValue)) && !errorStartTime) {
                    errorStartTime = new Date() * 1000;
                }
                // Делаем тик
                this.setState({
                    tick: !this.state.tick,
                    errorStartTime
                });
            },
            this.state.tickInterval * 1000
        );
        this.cells = Array.apply(null, Array(this.state.cellsPerLine * this.state.linesCount)).map(x => 0);

        this.setState({
            started: true,
            result: false
        })
    }

    onChange(key, value) {
        if ([
                'cellsPerLine',
                'linesCount',
                'tickInterval',
                'stepBackward',
                'stepForward',
                'maxValue'
            ].includes(key)) {
            this.setState({
                [key]: value
            });
            return;
        }
        const cell = this.state.cell;
        cell[key] = value;
        this.setState({ cell });
    }

    getLine(row) {
        const cells = [];
        for (let i = 0; i < this.state.cellsPerLine; i++) {
            const index = row * this.state.cellsPerLine + i;
            if (!(this.cells.length > index)) {
                this.cells.push(0);
            }
            cells.push(<Cell key={ i } { ...this.state.cell } value={ this.cells[index] } />);
        }
        return cells;
    }

    getLines() {
        const lines = [];
        for (let i = 0; i < this.state.linesCount; i++) {
            lines.push(<div className="panel__line" key={ i }>{ this.getLine(i) }</div>);
        }
        return lines;
    }

    render() {
        return (
            <div className="page">
                { this.state.result !== false && <h1 className="col-md-12 start-btn">Результат: { this.state.result }</h1> }
                { !this.state.started &&
                [
                    <ExperimentForm key="form1" { ...this.state } onChange={ this.onChange } />,
                    <ModelForm key="form2" { ...this.state } onChange={ this.onChange } />
                ]
                }
                <button className="btn btn-success start-btn" onClick={ this.start }>
                    { this.state.started && <span>Закончить эксперимент</span> }
                    { !this.state.started && <span>Начать эксперимент</span> }
                </button>
                <div className="panel">
                    { this.getLines() }
                </div>
            </div>
        );
    }
}

export default App;


