import React, { PropTypes } from 'react';
import FormView from './formView';
import InputField from './inputField';
import Select from './selectField';


const COLORS = [
    { value: 'red', label: 'Красный' },
    { value: 'orange', label: 'Оранжевый' },
    { value: 'yellow', label: 'Желтый' },
    { value: 'green', label: 'Зеленый' },
    { value: 'lightblue', label: 'Голубой' },
    { value: 'blue', label: 'Синий' },
    { value: 'violet', label: 'Фиолетовый' },
    { value: 'white', label: 'Белый' },
    { value: 'black', label: 'Черный' }  
];


export default (props) => {
    const { onChange, linesCount, cellsPerLine, cell } = props;
    return (
        <FormView title="Параметры модели">
            <InputField label="Количество строк" value={ linesCount }
                        onChange={ onChange } id="linesCount"
            />
            <InputField label="Количество ячеек в строке" value={ cellsPerLine }
                        onChange={ onChange } id="cellsPerLine"
            />
            <InputField label="Высота ячейки" value={ cell.height }
                        onChange={ onChange } id="height"
            />
            <InputField label="Ширина ячейки" value={ cell.width }
                        onChange={ onChange } id="width"
            />
            <InputField label="Размер шрифта" value={ cell.fontSize }
                        onChange={ onChange } id="fontSize"
            />

            <Select
                options={ COLORS }
                label="Цвет заливки"
                value={ cell.background }
                onChange={ onChange }
                id="background"
            />
            <Select
                options={ COLORS }
                label="Цвет значения"
                value={ cell.color }
                onChange={ onChange }
                id="color"
            />
            <Select
                options={ COLORS }
                label="Цвет границы"
                value={ cell.borderColor }
                onChange={ onChange }
                id="borderColor"
            />
        </FormView>
    );
}


